import React from "react";
import Info from "./components/info";
import Form from "./components/form";
import InfoCountry from "./components/infoCountry";

/*const API_KEY = "74e419c8f0bdc2acdc0658376ae8d3c4";*/

class App extends React.Component {

  state = {
    country:undefined,
    capital:undefined,
    region:undefined,
    subregion:undefined,
    population:undefined,
    area:undefined,
    error: undefined
  }

  gettingCountry = async (e) => {
    e.preventDefault();
    var name = e.target.elements.country.value;
    
    if(name) {
      const api_url = await fetch(`https://covid-api.mmediagroup.fr/v1/cases?country=&{name}`);
      const data = await api_url.json();

      /*var sunset = data.sys.sunset;
      var date = new Date();
      date.setTime(sunset);
      var sunset_date = date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();*/

      this.setState({
        country:data.All.country,
        capital:data.All.capital_city,
        region:data.All.continent,
        subregion:data.All.location,
        population:data.All.population,
        area:data.All.sq_km_area,
        error: undefined
      });
    } else {
      this.setState({
        country:undefined,
        capital:undefined,
        region:undefined,
        subregion:undefined,
        population:undefined,
        area:undefined,
        error: "Enter the country name"
      });
      }
  }

  render() {
    return(
      <div className="wrapper">
       <div className="main">
        <div className="container">
          
            <div className="info">
              <Info />
            </div>
            <div className="form">
              <Form countryMethod={this.gettingCountry} />
              <InfoCountry 
                country={this.state.country}
                capital={this.state.capital}
                region={this.state.region}
                subregion={this.state.subregion}
                population={this.state.population}
                area={this.state.area}
                error={this.state.error}
              />
            </div>
          
         </div>
        </div>
      
      
      </div>
    );
  }
}

export default App;